// src/auth.js
import NextAuth from "next-auth"
import OIDCProvider from "next-auth/providers/oidc"
import CredentialsProvider from "next-auth/providers/credentials"

import { buildMockSsoProfile } from "@/lib/mock-sso"

const hasOidcConfig = Boolean(
  process.env.AUTH_OIDC_ISSUER &&
  process.env.AUTH_OIDC_ID &&
  process.env.AUTH_OIDC_SECRET,
)

const providers = [
  CredentialsProvider({
    id: "dummy-sso",
    name: "Dummy SSO", // 표시용 이름 (NextAuth 로그 등)
    credentials: {},
    async authorize() {
      return buildMockSsoProfile({
        loginStrategy: "mock", // 디버깅 용 claim
      })
    },
  }),
]

if (hasOidcConfig) {
  providers.push(
    OIDCProvider({
      id: "oidc",
      name: "Enterprise OIDC",
      issuer: process.env.AUTH_OIDC_ISSUER,
      clientId: process.env.AUTH_OIDC_ID,
      clientSecret: process.env.AUTH_OIDC_SECRET,
      authorization: {
        params: {
          scope: "openid profile email",
        },
      },
      profile(profile) {
        return {
          id: profile.sub ?? profile.id ?? profile.user_id ?? profile.email ?? "oidc-user",
          name:
            profile.name ??
            profile.preferred_username ??
            profile.nickname ??
            profile.given_name ??
            "",
          email: profile.email ?? "",
          image: profile.picture ?? profile.avatar ?? null,
        }
      },
      checks: ["pkce", "state"],
    }),
  )
}

export const authOptions = {
  providers,
  session: {
    strategy: "jwt",
  },
  pages: {
    signIn: "/login",
  },
  callbacks: {
    async jwt({ token, user }) {
      if (user) {
        token.sub = user.id ?? token.sub ?? "user"
        token.name = user.name ?? token.name
        token.email = user.email ?? token.email
        token.picture = user.image ?? token.picture ?? null
      }
      return token
    },
    async session({ session, token }) {
      if (token) {
        session.user = {
          id: token.sub ?? token.id ?? "",
          name: token.name ?? session.user?.name ?? "",
          email: token.email ?? session.user?.email ?? "",
          image: token.picture ?? session.user?.image ?? null,
        }
      }
      return session
    },
  },
  secret: process.env.AUTH_SECRET ?? process.env.NEXTAUTH_SECRET,
}

export const { handlers, auth, signIn, signOut } = NextAuth(authOptions)

export const { GET, POST } = handlers
