// src/lib/mock-sso.js
/**
 * Mock SSO user profile used by the in-app dummy provider and API response.
 * Keeping it in a dedicated module allows us to reuse the same structure
 * between NextAuth configuration and the mock API route without duplication.
 */
export const MOCK_SSO_USER = {
  id: "mock-sso-user",
  name: "Mock SSO User",
  email: "mock.sso@example.com",
  image: null,
};

/**
 * Return a copy of the mock SSO user so individual callers can extend it
 * (e.g. append additional claims) without mutating the shared constant.
 */
export function buildMockSsoProfile(overrides = {}) {
  return {
    ...MOCK_SSO_USER,
    ...overrides,
  };
}
